.home_screen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100vh;
  padding: 0 29px;
  font-size: 12px;
  font-weight: 500;
  color: white;
}
.home_screen .top_inner {
  display: flex;
  flex-direction: column;
  gap: 19px;
  /* About & Work List*/
}
.home_screen .top_inner .top_contents {
  display: flex;
  justify-content: center;
  gap: 19px;
  padding-top: 24px;
}
.home_screen .top_inner .top_contents .about {
  text-align: center;
}
.home_screen .top_inner .top_contents .about .about_icon {
  overflow: hidden;
  width: 158px;
  height: 158px;
  margin-bottom: 5px;
  border-radius: 21px;
}
.home_screen .top_inner .top_contents .work_list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 19px;
  text-align: center;
}
.home_screen .top_inner .top_contents .work_list :nth-child(2) img,
.home_screen .top_inner .top_contents .work_list :nth-child(3) img {
  padding: 0 5px;
}
.home_screen .top_inner .top_contents .work_list .work_icon {
  overflow: hidden;
  width: 60px;
  height: 60px;
  margin: auto;
  margin-bottom: 5px;
  border-radius: 14px;
  background-color: #fff;
  align-content: center;
}

/* Home Widgets */
.home_widegets {
  text-align: center;
}
.home_widegets .inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 158px;
  padding: 12px 16px;
  margin-bottom: 5px;
  border-radius: 21px;
  background-image: linear-gradient(180deg, rgb(16, 88, 178) 0%, rgb(49, 155, 230) 100%);
  /* font-family: "SF-Pro-Text-Semibold"; */
  font-size: 14px;
}
.home_widegets .current_weather {
  display: flex;
  justify-content: space-between;
}
.home_widegets .current_weather .current_temp {
  font-size: 40px;
  text-align: start;
}
.home_widegets .icon_description_box {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
}
.home_widegets .icon_description_box .weather_icon img {
  width: 25px;
  filter: brightness(10);
}
.home_widegets .hourly_weather {
  display: flex;
  justify-content: space-between;
}
.home_widegets .hourly_weather li {
  display: flex;
  flex-direction: column;
  gap: 3px;
}
.home_widegets .hourly_weather li .time {
  font-size: 12px;
  color: #eee;
}
.home_widegets .hourly_weather li .time-icon img {
  width: 25px;
  filter: brightness(10);
}

